import { Component, OnInit } from '@angular/core';
import { ApiService } from './api.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'mongo-ui-latest';
  docs: any;
  tabs = [];
  activeTabIndex = 0;
  constructor(private Api: ApiService) {}
  ngOnInit() {
    this.Api.getDocumentsByCollection('local', 'startup_log').subscribe(
      (documents: []) => {
        this.docs = documents;
      }
    );
  }
  openTab(index) {
    this.activeTabIndex = index;
  }
  open(obj) {
    const tabIndex = this.tabs.findIndex(
      tab => tab.collection === obj.collectionName
    );
    if (tabIndex > -1) {
      this.activeTabIndex = tabIndex;
      return;
    }
    this.tabs.push({
      id: Date.now(),
      database: obj.dbName,
      collection: obj.collectionName
    });
    this.activeTabIndex = this.tabs.length - 1;
  }
  closeTab(id) {
    const idx = this.tabs.findIndex(tab => tab.id === id);
    this.tabs.splice(idx, 1);
    if (this.tabs.length) {
      this.activeTabIndex = this.tabs.length - 1;
    }
  }
}
